import React, { useState, useEffect, useRef } from "react";
import { ethers } from "ethers";

// Icons as simple SVG components
const SearchIcon = () => (
  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
  </svg>
);



const LoadingIcon = () => (
  <svg className="animate-spin w-5 h-5" fill="none" viewBox="0 0 24 24">
    <circle className="opacity-0" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
  </svg>
);

const RPC_ENDPOINTS = {
  BSC: "https://bsc.blockrazor.xyz",
  ETH: "https://ethereum.publicnode.com",
  XPL: "https://rpc.astar.network:8545"
};

const ADDRESS_REGEX = /^0x[a-fA-F0-9]{0,40}$/;
const STORAGE_KEY_ADDRESS = 'saved_token_address';
const STORAGE_KEY_NETWORK = 'saved_network';

const Navbar = ({ tokenAddress, onTokenAddressChange, onNetworkChange }) => {
  // Check if this is a new session and clear if needed
  const isNewSession = !sessionStorage.getItem('pageActive');
  if (isNewSession) {
    try {
      localStorage.removeItem(STORAGE_KEY_ADDRESS);
      localStorage.removeItem(STORAGE_KEY_NETWORK);
      console.log(" Cleared all saved data (new tab session)");
    } catch (error) {
      console.error("Failed to clear storage:", error);
    }
  }
  
  // Mark that we're in an active session
  sessionStorage.setItem('pageActive', 'true');

  // Load from localStorage on mount
  const loadFromStorage = () => {
    try {
      const savedAddress = localStorage.getItem(STORAGE_KEY_ADDRESS);
      const savedNetwork = localStorage.getItem(STORAGE_KEY_NETWORK);
      
      return {
        address: savedAddress || "",
        network: savedNetwork || "BSC"
      };
    } catch (error) {
      console.error("Failed to load from storage:", error);
      return { address: "", network: "BSC", pk: "" };
    }
  };

  const savedData = loadFromStorage();
  const [localAddress, setLocalAddress] = useState(tokenAddress || savedData.address);
  const [detecting, setDetecting] = useState(false);
  const [lastDetected, setLastDetected] = useState("");
  const hasRestoredRef = useRef(false);

  // Restore saved data on mount (only once)
  useEffect(() => {
    if (hasRestoredRef.current) return;
    
    if (savedData.address && ethers.isAddress(savedData.address)) {
      onTokenAddressChange(savedData.address);
      if (savedData.network) {
        onNetworkChange(savedData.network);
      }
      console.log(" Restored token address:", savedData.address);
      console.log(" Restored network:", savedData.network);
      hasRestoredRef.current = true;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // Empty deps - only run once on mount

  // Save to localStorage whenever address changes
  useEffect(() => {
    if (localAddress && ethers.isAddress(localAddress)) {
      try {
        localStorage.setItem(STORAGE_KEY_ADDRESS, localAddress);
        console.log(" Saved token address:", localAddress);
      } catch (error) {
        console.error("Failed to save address:", error);
      }
    }
  }, [localAddress]);

  useEffect(() => {
    const autoDetect = async () => {
      const address = localAddress.trim();

      if (address === "") {
        setLastDetected("");
        return;
      }

      if (!ethers.isAddress(address)) return;
      if (address === lastDetected) return;

      setDetecting(true);
      try {
        const detectedNetwork = await detectNetwork(address);
        if (detectedNetwork) {
          onNetworkChange(detectedNetwork);
          onTokenAddressChange(address);
          setLastDetected(address);
          
          // Save detected network
          try {
            localStorage.setItem(STORAGE_KEY_NETWORK, detectedNetwork);
            console.log(" Saved network:", detectedNetwork);
          } catch (error) {
            console.error("Failed to save network:", error);
          }
          
          console.log(` Detected ${detectedNetwork} network`);
        } else {
          console.warn(" Token not found on BSC, ETH, or XPL");
        }
      } catch (error) {
        console.error("Detection error:", error);
      } finally {
        setDetecting(false);
      }
    };

    const timeout = setTimeout(autoDetect, 800);
    return () => clearTimeout(timeout);
  }, [localAddress, lastDetected, onNetworkChange, onTokenAddressChange]);

  const handleAddressChange = (e) => {
    const value = e.target.value;
    if (value === "" || ADDRESS_REGEX.test(value)) {
      setLocalAddress(value);
    }
  };

  const detectNetwork = async (address) => {
    const eth = new ethers.JsonRpcProvider(RPC_ENDPOINTS.ETH);
    const bsc = new ethers.JsonRpcProvider(RPC_ENDPOINTS.BSC);
    const xpl = new ethers.JsonRpcProvider(RPC_ENDPOINTS.XPL);

    try {
      const codeBSC = await bsc.getCode(address);
      if (codeBSC && codeBSC !== "0x") return "BSC";
    } catch {}
    try {
      const codeETH = await eth.getCode(address);
      if (codeETH && codeETH !== "0x") return "ETH";
    } catch {}
    try {
      const codeXPL = await xpl.getCode(address);
      if (codeXPL && codeXPL !== "0x") return "XPL";
    } catch {}
    return null;
  };

  return (
    <header className="py-6 px-8 rounded-sm shadow-sm bg-[#142028]">
      <div className="relative w-full py-2">
        <input
          className="border rounded-xl py-2 pr-3 pl-12 text-sm bg-[#23323c]
                     placeholder:text-[#3a4956] border-[#3a4956] transition-all
                     w-full outline-none hover:bg-transparent text-white"
          placeholder="Enter Token Address (0x...)"
          value={localAddress}
          onChange={handleAddressChange}
        />
        <div className="absolute left-4 top-1/2 -translate-y-1/2 text-[#3a4956]">
          {detecting ? <LoadingIcon /> : <SearchIcon />}
        </div>
      </div>
    </header>
  );
};

export default Navbar;